import React, { useState, useRef, useEffect } from 'react';
import { useMutation } from '@apollo/client';
import { SEND_MESSAGE } from '../graphql/queries';
import { createHttpClient } from '../graphql/client';
import { Message, ChatMessage, SendMessageVariables, ChatResponse } from '../types';
import MessageBubble from './MessageBubble';
import MessageInput from './MessageInput';
import TypingIndicator from './TypingIndicator';

const ChatInterface: React.FC = () => {
  const [messages, setMessages] = useState<Message[]>([
    {
      id: '1',
      content: ' DeepSeek AI ',
      sender: 'ai',
      timestamp: new Date(),
    },
  ]);
  const [isTyping, setIsTyping] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const messagesEndRef = useRef<HTMLDivElement>(null);
  
  // GraphQL mutation hook
  const [sendMessageMutation] = useMutation<ChatResponse, SendMessageVariables>(SEND_MESSAGE);
  
  // HTTP 
  const httpClient = createHttpClient();

  // 
  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  };

  useEffect(() => {
    scrollToBottom();
  }, [messages, isTyping]);

  //  UI  API 
  const convertToApiMessages = (uiMessages: Message[]): ChatMessage[] => {
    return uiMessages.map(msg => ({
      role: msg.sender === 'user' ? 'user' : 'assistant',
      content: msg.content,
    }));
  };

  //  GraphQL  DeepSeek API
  const callDeepSeekViaGraphQL = async (content: string): Promise<string> => {
    const apiMessages: ChatMessage[] = [
      // 
      {
        role: 'system',
        content: 'AI Markdown '
      },
      //  10 
      ...convertToApiMessages(messages.slice(-10)),
      // 
      {
        role: 'user',
        content: content
      }
    ];

    try {
      const result = await sendMessageMutation({
        variables: {
          input: {
            messages: apiMessages,
            model: 'deepseek-chat',
            max_tokens: 2000,
            temperature: 0.7,
            top_p: 0.95,
          }
        }
      });

      if (result.data?.choices[0]?.message.content) {
        return result.data.choices[0].message.content;
      } else {
        throw new Error(' AI ');
      }
    } catch (error: any) {
      console.error('GraphQL API :', error);
      throw new Error(`API : ${error.message}`);
    }
  };

  //  HTTP 
  const callDeepSeekViaHTTP = async (content: string, useStream = false): Promise<string> => {
    debugger;
    const apiMessages: ChatMessage[] = [
      {
        role: 'system',
        content: 'AI Markdown '
      },
      ...convertToApiMessages(messages.slice(-10)),
      {
        role: 'user',
        content: content
      }
    ];

    try {
      const response = await httpClient.post('/api/chat', {
        messages: apiMessages,
        model: 'deepseek-chat',
        max_tokens: 2000,
        temperature: 0.7,
        top_p: 0.95,
        stream: useStream,
      });
      console.log(response,111)
      return response.choices[0]?.message?.content || 'AI ';
    } catch (error: any) {
      console.error('HTTP API :', error);
      throw new Error(`API : ${error.message}`);
    }
  };

  // 
  const handleSendMessage = async (content: string) => {
    if (!content.trim()) return;

    // 
    setError(null);

    // 
    const userMessage: Message = {
      id: Date.now().toString(),
      content: content.trim(),
      sender: 'user',
      timestamp: new Date(),
    };

    setMessages(prev => [...prev, userMessage]);
    setIsTyping(true);

    try {
      //  GraphQL  HTTP 
      //  GraphQL
      const useGraphQL = true;
      
      let aiResponse: string;
      
      if (useGraphQL) {
        aiResponse = await callDeepSeekViaGraphQL(content);
      } else {
        aiResponse = await callDeepSeekViaHTTP(content);
      }
      
      const aiMessage: Message = {
        id: (Date.now() + 1).toString(),
        content: aiResponse,
        sender: 'ai',
        timestamp: new Date(),
      };

      setMessages(prev => [...prev, aiMessage]);
    } catch (error: any) {
      console.error(':', error);
      setError(error.message);
      
      // 
      const errorMessage: Message = {
        id: (Date.now() + 1).toString(),
        content: '',
        sender: 'ai',
        timestamp: new Date(),
      };
      
      setMessages(prev => [...prev, errorMessage]);
    } finally {
      setIsTyping(false);
    }
  };

  // 
  const clearError = () => {
    setError(null);
  };

  return (
    <div className="chat-container">
      {/*  */}
      <div className="chat-header">
        <h1 className="chat-title">DeepSeek AI </h1>
        {error && (
          <div className="error-banner" onClick={clearError}>
            <span className="error-text"> {error}</span>
            <span className="error-close"></span>
          </div>
        )}
      </div>

      {/*  */}
      <div className="chat-messages">
        {messages.map((message) => (
          <MessageBubble key={message.id} message={message} />
        ))}
        
        {isTyping && <TypingIndicator />}
        
        <div ref={messagesEndRef} />
      </div>

      {/*  */}
      <MessageInput 
        onSendMessage={handleSendMessage} 
        disabled={isTyping}
        placeholder={isTyping ? "AI ..." : "..."}
      />
    </div>
  );
};

export default ChatInterface;